import React from "react"
import { Link } from "gatsby"
import { Helmet } from "react-helmet"

import "../styles/style.css"
import TeamImg from "../images/lunacats_team.jpg"
import RobotImg from "../images/lunacats_robot.jpg"

export default function Project() {
  	return (
	  	<>
	  		<Helmet>
	  			<title>Projects</title>
	  		</Helmet>
	  		<header></header>
	  		<main>
			    <h1>Some of my Past and Present Projects</h1>
	    
		        <div className="projectA">
		          	<h3>UNH Lunacats</h3>

		          	<div className="slideshow-container">
			            <div className="mySlides fade">
			              <div className="numbertext">1 / 2</div>
			              <img className="proj-images" src={TeamImg} alt="Lunacats team photo"></img>
			              <div className="text">Lunacats team photo at Kennedy Space Center (2018)</div>
			            </div>

			            <div className="mySlides fade">
			              <div className="numbertext">2 / 2</div>
			              <img className="proj-images" src={RobotImg} alt="Lunacats robot photo"></img>
			              <div className="text">Lunacats robot in testing pit (2017)</div>
			            </div>

			            <a className="prev" onclick="plusSlides(-1)">&#10094;</a>
			            <a className="next" onclick="plusSlides(1)">&#10095;</a>
		          	</div>

		          	<div className="proj-img-selecter">
			            <span className="dot" onclick="currentSlide(1)"></span>
			            <span className="dot" onclick="currentSlide(2)"></span>
		          	</div>
		          	<p>My senior year of my undergraduate degree I was the mechanical team lead 
			            for the UNH Lunacats. The team designs and builds a robot to compete in the 
			              NASA Robotic Mining Competition (RMC) at the Kennedy Space Center in Cape 
			            Canaveral, Florida. The competition drives progress for new and unique rover 
			            designs to accomplish complex tasks. The goal of the competition is to 
			            efficiently mine and deposit as much icy regolith as possible. More points 
			            are scored for more regolith mined in the time limit, less energy used, more 
			            autonomy used, better dust protection, and a lighter robot. My work on the 
			            robot was redesigning and building the mining and depositing apparatus as 
			            well as doing scientific outreach in the community around UNH to get more 
			            people interested in STEM fields. This was a fun project and the team worked 
			            together very well and built a pretty good robot. The robot still had some 
			            difficulties at competition but it gets better each year! I am still an 
			            active member of this team although my capacity is now as graduate student 
			            advisor. More information about the competition can be found 
			            <Link to="https://www.nasa.gov/offices/education/centers/kennedy/technology/nasarmc.html">here</Link>.
		          	</p>
		          	<p><Link to="../">Go back to home page</Link></p>
		        </div>
	  		</main>
	  	</>
  	);
}
